.mainItem{
    display: flex;
}
.avatar{
    width: 50px;
    margin-bottom: 2px;
}
.avatarInfo{
    display: flex; 
    margin-bottom: 5px;
    flex-direction: column; 
    margin-left: 10px;
    align-items: center;
    & button{
        width: 90px;
    }
}
.city{
    flex-direction: column;
}
.userInfo{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.container{
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.status{
    width: 300px;
    color: rebeccapurple;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
p{
    margin: 0;
}
